<template>
  <el-container v-loading="loading">
    <div class="box">
      <h3>{{activeheader}}</h3>
      <div class="input-box">
        <span></span>
        <label for="" class="label require">医院名称：</label>
        <el-input class="input-width" v-model="name" placeholder="请输入内容"></el-input>
      </div>
      <div class="input-box">
        <span></span>
        <label for="" class="label">医院别名：</label>
        <el-input class="input-width" v-model="otherName" placeholder="请输入内容"></el-input>
      </div>
      <div class="input-box">
          <label class="label require">医院类型:</label>
          <el-select v-model="type" placeholder="请选择">
            <el-option
              v-for="item in hopType"
              :key="item.id"
              :label="item.dValue"
              :value="item.dKey">
            </el-option>
          </el-select>
      </div>
      <div class="input-box">
          <label class="label require">医院等级:</label>
          <el-select v-model="level" placeholder="请选择">
            <el-option
              v-for="item in hosLevelList"
              :key="item.id"
              :label="item.dValue"
              :value="item.dKey">
            </el-option>
          </el-select>
      </div>
      <div class="input-box">
          <label class="label require">所在地区:</label>
          <user-address :defaultValue="defaultAddress" ref="hosAddAddress" seletStyle="width:217px"/>
      </div>
      <div class="input-box">
        <span></span>
        <label for="" class="label">医院电话：</label>
        <el-input class="input-width" v-model="phone" placeholder="请输入内容"></el-input>
      </div>
      <div class="input-box">
        <span></span>
        <label for="" class="label">医院地址：</label>
        <el-input class="input-width" v-model="address" placeholder="请输入内容"></el-input>
      </div>
      <div class="input-box">
          <label class="label">医院简介：</label>
          <el-input
            type="textarea"
            :rows="2"
            size="medium"
            placeholder="请输入内容"
            v-model="info">
          </el-input>
        </div>
        <div class="input-box">
          <label for="" class="label"></label>
          <el-checkbox v-model="shebao" label= true >社保医院</el-checkbox>
        </div>
        <div class="input-box">
          <label class="label">备注信息：</label>
          <el-input
            type="textarea"
            :rows="2"
            size="medium"
            placeholder="请输入内容"
            v-model="remark">
          </el-input>
        </div>
        <div class="input-box box-last">
          <el-button type="primary" @click="submit">提交</el-button>
          <el-button @click="cancle">取消</el-button>
        </div>
    </div>
  </el-container>
</template>

<script>
import check from 'utils/check'
import { hopType, hosLevelList } from "data/listBody";
import UserAddress from "components/address";
let headers = ['新增医院','修改医院'];
let requireArray = ['name','type','level','province','city','country','shebao']; //必传字段

export default {
  name: 'hosAdd',
  data() {
    return {
      name: '',
      otherName: '',
      type: '',
      level: '',
      phone: '',
      address: '',
      remark: '',
      info: '',
      id: '',
      loading: false,
      shebao: false,
      defaultAddress: null, //默认地址
      activeheader: headers[0],
      check: check.methods.check,
      checkTip: check.tips.checkTip,
      hopType,
      hosLevelList,
    }
  },
  components: {
    UserAddress
  },
  mounted(){
    this.editInit();
  },
  methods: {
    editInit(){
      let { type, id } = this.$route.query;
      if( type === 'edit' ) this.activeheader = headers[1];
      if( !id ) return;
      this.loading = true;      
      this.api.get(`/hospital/${id}`).then((res) => { //查询产品详情
        this.dataInit(res.data)
        setTimeout(() => {
          this.loading = false;
        }, 20);
      }).catch((err) => {
        console.log(err)
        this.loading = false;
        this.$message(err.data.message)
      })
    },
    dataInit(data){
      data = data || {};
      let { name, otherName, type, level, phone, address, remark, info, id, province, city, country, shebao } = data;
      this.name = name;
      this.otherName = otherName;
      this.type = type;
      this.level = level;
      this.phone = phone;
      this.address = address;
      this.remark = remark;
      this.info = info;
      this.id = id;
      this.shebao = shebao;
      this.defaultAddress = {
        province,
        city,
        country
      }
    },
    submit(){
      let addressParams = this.$refs.hosAddAddress.getData();
      let params = {
          name: this.name,
          otherName: this.otherName,
          type: this.type,
          level: this.level,
          phone: this.phone,
          address: this.address,
          info: this.info,
          remark: this.remark,
          shebao: this.shebao,
      }
      let param = this.lodash.assign({}, params, addressParams);
      this.check(param, requireArray).then(()=>{ 
        this.addUser(param) 
      },()=>{ 
        this.$message(this.checkTip) 
      }).catch((err) => {
        console.log(err)
        this.$message(err.data.message)
      })
    },
    editUser(param){ //修改用户
      param.id = this.id;
      this.loading = true; 
      this.api.put('/hospital',param, 'form').then((res)=>{
        this.loading = false; 
        this.$router.go(-1);
      }).catch((err) => {
        console.log(err)
        this.loading = false; 
        this.$message(err.data.message)
      })
    },
    addUser(param){ //添加用户
      if(this.$route.query.type === 'edit') return this.editUser(param);
      this.loading = true; 
      this.api.post('/hospital', param, 'form').then((res)=>{
        this.$router.go(-1);
        this.loading = false; 
      }).catch((err) => {
        console.log(err)
        this.loading = false; 
        this.$message(err.data.message)
      })
    },
    cancle(){
      this.$router.go(-1)
    }
  }
}
</script>
 
<style lang="less" scoped>
.box{
  padding-left: 10px;
}
.input-box {
  display: flex;
  align-items: center;
  margin-bottom: 10px;
  &.box-last{
    margin-left: 20px;
  }
}
.label{
  width: 100px;
  min-width: 100px;
  margin-left: 20px;
  &.require{
    position: relative;
    &::before{
        content: '*';
        color: red;
        position: absolute;
        left: -10px;
        top: 4px;
    }  
  }
}
.label-left{
  margin-left: 15px;
}
.input-width{
  width: 216px;
}
.el-textarea{
  width: 300px;
}
.el-select{
  margin-right: 10px;
}
.box-last{
  margin-top: 20px;
}
</style>
 